.contact-card__field input{
  border: 0;
  outline: 0;
  padding: 5px;
  background: rgba(35,35,35,0.025);
  -webkit-box-shadow: 0px 0px 1px 0px rgba(35,35,35,0.15);
          box-shadow: 0px 0px 1px 0px rgba(35,35,35,0.15);
  -webkit-transition: -webkit-box-shadow 0.15s ease;
  transition: -webkit-box-shadow 0.15s ease;
  -o-transition: box-shadow 0.15s ease;
  transition: box-shadow 0.15s ease;
  transition: box-shadow 0.15s ease, -webkit-box-shadow 0.15s ease;
  font-weight: 300;
}

.contact-card__field input:focus{
  -webkit-box-shadow: 0px 0px 1px 0px rgba(35,35,35,0.15), 0px 1px 0px 0px #5181DC;
          box-shadow: 0px 0px 1px 0px rgba(35,35,35,0.15), 0px 1px 0px 0px #5181DC;
}



