.icon-arrow{
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 1px solid rgba(35,35,35,1);
  border-radius: 20px;
  position: relative;
}

.icon-arrow__strokes {
  height: 8px;
  width: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);

}

.icon-arrow__strokes  > * {
  position: absolute;
  height: 1px;
  background: rgba(35,35,35,1);
}

.icon-arrow__strokes  > *:first-child{
  width: 100%;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}

.icon-arrow__strokes  > *:nth-child(2){
  top: 2px;
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
  right: 0;
  width: 4px;
}

.icon-arrow__strokes  > *:nth-child(3){
  bottom: 2px;
  width: 4px;
  right: 0;
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.icon-arrow.left .icon-arrow__strokes > *:nth-child(2){
  left: 0;
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.icon-arrow.left .icon-arrow__strokes > *:nth-child(3){
  left: 0;
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}



