.subheader{
  height: 65px;
  -webkit-box-shadow: inset 0px -1px 0px 0px rgba(35,35,35,0.3);
          box-shadow: inset 0px -1px 0px 0px rgba(35,35,35,0.3);
  overflow: hidden;
  width: 100vw;
}

.subheader h1 {
  line-height: 70px;
  font-weight: 300;
}

.subheader.dark-blue{
  background: #52688C;
}

.subheader.blue {
  background: #5181DC;
}

.subheader.green {
  background: #5BCBA9;
}

.subheader.orange {
  background: #FEB02C;
}

.subheader__spacer {
  height: 65px;
}

.subheader__spacer > div {
  height: 100%;
  width: 50%;
  border-right: 1px solid rgba(35,35,35,0.3);
}

.subheader__menu__list h4 {
  line-height: 70px;
  font-size: 12px;
  display: inline-block;
  margin: 0;
  padding-right: 50px;
  color: #FEFEFE;
  position: relative;
}

.subheader__menu__list a.active {
  font-weight: 500;
}

.subheader__menu__list a:after{
  content: '';
  height: 7px;
  position: absolute;
  bottom: 0;
  left: 0;
  width: calc(100% - 50px);
  background: #FEFEFE;
  -webkit-transform: translateY(7px);
      -ms-transform: translateY(7px);
          transform: translateY(7px);
  -webkit-transition: -webkit-transform 0.15s ease;
  transition: -webkit-transform 0.15s ease;
  -o-transition: transform 0.15s ease;
  transition: transform 0.15s ease;
  transition: transform 0.15s ease, -webkit-transform 0.15s ease;
}

.main-app .subheader__menu__list a:after{
  background: #F6F7F7;
}

.subheader__menu__list a.active:after{
  -webkit-transform: translateY(-5px);
      -ms-transform: translateY(-5px);
          transform: translateY(-5px);
  -webkit-box-shadow: 0px -1px 0px 0px rgba(0,0,0,.3),
    1px 0px 0px 0px rgba(0,0,0,.3),
    -1px 0px 0px 0px rgba(0,0,0,.3);
          box-shadow: 0px -1px 0px 0px rgba(0,0,0,.3),
    1px 0px 0px 0px rgba(0,0,0,.3),
    -1px 0px 0px 0px rgba(0,0,0,.3);
}

.subheader__buttons {
  height: 65px;
  position: relative;
  border-left: 1px solid rgba(35,35,35,0.1);
}

.subheader__buttons:after{
  height: 100%;
  background: rgba(35,35,35,0.05);
  max-width: 100vw;
  -ms-flex: 0 0 100vw;
      flex: 0 0 100vw;
  width: 100vw;
  position: absolute;
  left: 0;
  top: 0;
  content: '';
  z-index: 0;
}

.subheader__buttons .button{
  float: right;
  position: relative;
  z-index: 1;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  margin-right: 5px;
  margin-left: 5px;
}



