.admin-input-group input, .admin-input-group textarea{
  display: block;
  border: 0;
  outline: 0;
  background: #F6F7F7;
  width: 100%;
  padding: 15px;
  font-size: 18px;
  font-weight: 400;
  border-radius: 3px;
  -webkit-box-shadow: inset 0px -1px 0px 0px rgba(35,35,35,0.1),
  inset 0px 0px 3px 0px rgba(35,35,35,0.15);
          box-shadow: inset 0px -1px 0px 0px rgba(35,35,35,0.1),
  inset 0px 0px 3px 0px rgba(35,35,35,0.15);
}

.admin-input-group h3 {
  margin-bottom: 25px;
}

.admin-input-group {
  margin-bottom: 75px;
}



