.person__inputs{
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}

.person__inputs:after{
  display: block;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(50%, #EBEDED), color-stop(0%, rgba(255,255,255,0)));
  background-image: -webkit-linear-gradient(#EBEDED 50%, rgba(255,255,255,0) 0%);
  background-image: -o-linear-gradient(#EBEDED 50%, rgba(255,255,255,0) 0%);
  background-image: linear-gradient(#EBEDED 50%, rgba(255,255,255,0) 0%);
  background-position: right 0px;
  background-size: 1px 75px;
  background-repeat: repeat-y;
  content: '';
  height: 100%;
  width: 1px;
}

.person-new > .pdt-card{
  max-width: 600px;
  margin: 0 auto;
}

.person-grid .pdt-card:not(:first-child){
  margin-top: 10px;
}



